body{
  font-family: Playfair;
  /* background: linear-gradient(to left, #e4de6e, #b0bfc1) !important; */
  background: url(https://images.pexels.com/photos/19741018/pexels-photo-19741018.jpeg?auto=compress&cs=tinysrgb&fit=crop&h=1200&w=800);
  background-repeat:no-repeat;
  background-size:cover;
  /* background-position:center; */
}
.bg-dark1{
  /* color: #0a0a0a; */
  background: inherit;
  height: 82px;
}
.nav-link{
  color: rgb(0, 0, 0);
 border-bottom: 2px solid transparent;
}
#heading{
  text-shadow: 2px 2px 4px black;
}
* {
  box-sizing: border-box;
}

.header {
  text-align: center;
  padding: 32px;
}
.mainname{
 text-decoration: none ;
 color: white;
}
.ic{
  display: none;
  position: absolute;
  color: black;
  background-color: rgb(129, 129, 129,0.2);
  font-size: 16px;
  border: none;
  border-radius: 3.5rem;
  padding-bottom: 2px;
  cursor: pointer;
}
.download-button {
  top: 1rem;
}
.heart-button {
  top: 4rem;
}
.link-button{
  top: 7rem;
}
.column:hover .download-button {
  display: block;
}
.column:hover .heart-button {
  display: block;
}
.column:hover .link-button {
  display: block;
}

.download-button:hover {
  background-color: rgb(255, 255, 255);
}
.link-button:hover{
  background-color: rgb(255, 255, 255);

}
.heart-button:hover{
  background-color: rgb(255, 255, 255);

}
.hr:hover{
 color: red;
}

#about{
  box-shadow: 6px 6px 8px black;
 
}


